import React from "react";
import styled from "styled-components";
import line from "../../images/line.svg";
import { MiniCircle } from "./miniCircle";
export function Progress({ data }) {
    const converter = () => {
        if (data === "") {
            return 1;
        } else if (data === "   ") {
            return 2;
        } else if (data === "  ") {
            return 3;
        } else if (data === "") {
            return 4;
        } else if (data === "") {
            return 5;
        } else if (data === " ") {
            return 6;
        }
    };
    const status = converter();
    const selectStatus = num => {
        if (status === num) {
            if (num === 6) {
                return "complete";
            } else return "current";
        } else if (status > num) {
            return "complete";
        } else {
            return "default";
        }
    };
    return (
        <Container>
            <img style={{ marginTop: 26 }} src={line}></img>
            <Current>
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={1}
                    state={selectStatus(1)}
                    status=""
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={2}
                    state={selectStatus(2)}
                    status="   "
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={3}
                    state={selectStatus(3)}
                    status="  "
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={4}
                    state={selectStatus(4)}
                    status=""
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={5}
                    state={selectStatus(5)}
                    status=""
                />
                <MiniCircle
                    style={{ width: 90, left: -35 }}
                    number={6}
                    state={selectStatus(6)}
                    status=" "
                />
            </Current>
        </Container>
    );
}

const Container = styled.div``;
const Current = styled.div`
    display: flex;
    position: relative;
    top: -15px;
`;
