import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { RoundButton2 } from "../../components/0.Button/roundButton2";
import { RoundButton3 } from "../../components/0.Button/roundButton3";
import {
    API_SERVER2,
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL
} from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import useAsync from "../../hook/useAsync";
import projects from "../../images/projects.svg";
import { Mobile, NotMobile } from "../../lib/mediaQuery";
import { ArtistPagination } from "../main/4.BrowseArtist/3.ArtistPagination/artistPagination";
import { ProjectCard } from "./projectCard";

export function MyProjects({ currentSize }) {
    const chooseFilter = filter => {
        let sortKey = [];
        if (filter !== "nothing") sortKey.push(filter);
        setFilterBy(sortKey);
    };
    const [page, setPage] = useState(1);
    const changePage = num => {
        setPage(num);
        window.scrollTo(0, 360);
    };
    const [filterBy, setFilterBy] = useState([]);
    useEffect(() => {
        refetch(data);
    }, [page, filterBy]);
    const getProjects = async () => {
        const params = new URLSearchParams();
        params.append("page", page);
        if (filterBy.length !== 0) params.append("filterBy", filterBy);
        const userType = JSON.parse(
            window.localStorage.getItem("userData"),
        ).userType;
        const url = API_SERVER2 + `/projects/my/client`;
        const response = await axios.get(url, {
            withCredentials: true,
            params: params,
        });

        return response.data;
    };
    const history = useHistory();
    const [tabActive, setTabActive] = useState(" ");
    const changeActive = name => {
        setTabActive(name);
    };
    const [state, refetch] = useAsync(getProjects, []);
    const { data } = state;
    let pagination,
        project = null;
    if (data) {
        pagination = data.pagination;
        project = data.data.projects;
    }
    return (
        <Container>
            <InnerContainer>
                <Tab>
                    <RoundButton2
                        onClick={() => {
                            changeActive(" ");
                            chooseFilter("nothing");
                        }}
                        activate={tabActive === " "}
                        style={{ marginRight: 8 }}
                    >
                         
                    </RoundButton2>
                    <RoundButton2
                        onClick={() => {
                            changeActive(" ");
                            chooseFilter(" ");
                        }}
                        activate={tabActive === " "}
                        style={{ marginRight: 8 }}
                    >
                         
                    </RoundButton2>
                    <RoundButton2
                        onClick={() => {
                            changeActive(" ");
                            chooseFilter(" ");
                        }}
                        activate={tabActive === " "}
                        style={{ marginRight: 8 }}
                    >
                         
                    </RoundButton2>
                    <RoundButton2
                        onClick={() => {
                            changeActive("");
                            chooseFilter("");
                        }}
                        activate={tabActive === ""}
                        style={{ marginRight: 8 }}
                    >
                        
                    </RoundButton2>
                    <NotMobile>
                        <RoundButton3
                            onClick={() => {
                                history.push("/stage8/request/");
                                window.scrollTo(0, 0);
                            }}
                            style={{ marginLeft: "auto" }}
                        >
                            +   
                        </RoundButton3>
                    </NotMobile>
                </Tab>
                {data &&
                    (project === undefined ? (
                        <EmptyProject>
                            <ProjectImage src={projects} />
                            <Text1>  .</Text1>
                            <div
                                style={{
                                    marginTop: 4,
                                    marginBottom: 32,
                                    display: "flex",
                                    flexDirection: "column",
                                    alignItems: "center",
                                }}
                            >
                                <TextBox>
                                    <Text3>
                                        {
                                            "-     !"
                                        }
                                    </Text3>
                                    <Text2>
                                        {
                                            "  >  []   >  "
                                        }
                                    </Text2>
                                </TextBox>
                                <NotMobile>
                                    <Text3>
                                        -    
                                        ?   
                                          STAGE8  
                                        !
                                    </Text3>
                                </NotMobile>
                                <Mobile>
                                    <Text3>
                                        -    
                                        ? <br />
                                            
                                        <br /> STAGE8  
                                        !
                                    </Text3>
                                </Mobile>
                            </div>
                            <RoundButton3
                                onClick={() => {
                                    history.push("/request/stage8");
                                    window.scrollTo(0, 0);
                                }}
                            >
                                 
                            </RoundButton3>
                        </EmptyProject>
                    ) : (
                        <Cards>
                            {project.map((data, i) => {
                                return (
                                    <ProjectCard
                                        currentSize={currentSize}
                                        key={i}
                                        data={data}
                                    ></ProjectCard>
                                );
                            })}
                        </Cards>
                    ))}
                {data &&
                    pagination !== undefined &&
                    pagination.lastPage > 1 && (
                        <ArtistPagination
                            pagination={pagination}
                            setPage={changePage}
                        />
                    )}
            </InnerContainer>
        </Container>
    );
}
const TextBox = styled.div`
    display: flex;
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }
`;
const Cards = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 20px;
    }
`;
const Text3 = styled.div`
    text-align: center;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 20, 400, "#DBDBDB")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400, "#DBDBDB", "0em")};
    }
`;
const Text1 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 32px;
        ${SpoqaHansSansNeo(34, 40, 700, "#DBDBDB")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 20px;
        ${SpoqaHansSansNeo(20, 24, 700, "#DBDBDB")};
    }
`;
const Text2 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 20, 700, "#DBDBDB")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(12, 14, 700, "#DBDBDB", "0em")};
    }
`;
const EmptyProject = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`;
const Tab = styled.div`
    width: 100%;
    display: flex;
`;
const InnerContainer = styled.div`
    flex-wrap: wrap;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
        min-height: 970px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
        min-height: 970px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 100%;
        min-height: 970px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
    }
    padding: 0px 20px 80px 20px;
`;
const Container = styled.div`
    ${DisplayFlex("center", "")};
`;
const ProjectImage = styled.img`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 200px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 96px;
        height: 91px;
        margin-top: 75px;
    }
`;
